/* Default styles for the main container */
.container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 20px;
  background-color: #f5f5f5;
}


.container h1{
    white-space: normal;
    word-break: keep-all;
}

.sideb {
    position: fixed;
  top: 0;
  left: 0;
  width: 250px;
  background-color: #000000;
  height:1000dvh;
}


.sideb h1{
  color:#ffffff;
}
.sideb li{
  padding-bottom: 15px;
}
.sidebar a{
   color: #ffffff;
   text-decoration:none;
   font-size: 24px;
}
.sidebar a:hover{
   color: #ff9900;
   
}

/* Style for the Navi container */
.Navi {
  display: flex;
  justify-content: space-between; /* This will push the "toggle" to the left and "menubox" to the right */
  align-items: center; /* Vertically center the contents */
  background-color: #ffffff; /* Background color for the Navi container */
  padding: 10px; /* Padding for the Navi container */
}

/* Style for the toggle icon (hamburger menu) */
.toggle {
  font-size: 24px;
  cursor: pointer;
}
.toggle:hover {
  color: #ff9900;
}

/* Style for the menubox */
.menubox {
  text-align: center; /* Center the text horizontally within the menubox */
  flex-grow: 1; /* Allow the menubox to take up available space */
}

/* Style for the menu items */
.menu {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.menu li {
  display: inline; /* Display the list items horizontally */
  margin-right: 20px; /* Spacing between menu items */
}

/* Style for menu item links */
.menu li a {
  text-decoration: none;
  color: #000000; /* Text color for menu items */
  font-weight: bold;
  font-size: 18px;
}

/* Style for the currently active link */
.menu li a:hover {
  color: #ff9900; /* Change the color of the active link */
}


  .cross {
  position: absolute;
  top: 0;
  right: 0;
  padding: 10px;
  color: #fff; /* Adjust the color as needed */
  cursor: pointer;
  font-size: 30px;
 
}
/* Styles for smaller screens (e.g., tablets and mobile devices) */
@media (max-width: 768px) {
  .container {
    padding: 10px; /* Reduce padding for smaller screens */
  }
}

/* Styles for even smaller screens (e.g., smartphones) */
@media (max-width: 480px) {
  .container {
    padding: 5px; /* Further reduce padding for very small screens */
  }
}